export function diaplayTime(dateTimeStamp) {
	let newT = dateTimeStamp.replace(/-/g, '/'); //ios
	let result;
	let minute = 1000 * 60;
	let hour = minute * 60;
	let day = hour * 24;
	let halfamonth = day * 15;
	let month = day * 30;
	let now = new Date().getTime();
	let curTime = new Date(newT);
	let diffValue = now - curTime;
	if (diffValue < 0) {
		return;
	}
	let monthC = diffValue / month;
	let weekC = diffValue / (7 * day);
	let dayC = diffValue / day;
	let hourC = diffValue / hour;
	let minC = diffValue / minute;
	if (monthC >= 1) {
		if (monthC <= 12)
			result = "" + parseInt(monthC) + "";
		else {
			result = "" + parseInt(monthC / 12) + "";
		}
	} else if (weekC >= 1) {
		result = "" + parseInt(weekC) + "";
	} else if (dayC >= 1) {
		result = "" + parseInt(dayC) + "";
	} else if (hourC >= 1) {
		result = "" + parseInt(hourC) + "";
	} else if (minC >= 1) {
		result = "" + parseInt(minC) + "";
	} else {
		result = "";
	}
	return result;
}
